
nav {
  background-color: #222;
  color: white;
  padding: 50px 15px;
  font-size:250%;
  display: flex;
  height: auto;
  justify-content: space-between;
  align-items: center;
  position:sticky;
}

.logo h2 {
  font-size: 150%;
  margin: 0;
  color: white;
}

.nav-links {
  display: flex;
  gap: 80px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #00bcd4;
}
