
.projects-section {
    height: 100vh;

  background-image: linear-gradient(
      rgba(0, 0, 0, 0.5), 
      rgba(0, 0, 0, 0.5)
    ),
    url('E:\REACT\Portfolio\my-app\src\Images\img2.jpg');
  background-size: cover;
  background-position: center;
  flex-direction: column;
  font-size: 1.5rem;
  justify-content: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
  color: rgb(18, 4, 4);
  text-shadow: 1px 1px 3px rgba(0,0,0,0.8);
}

.projects-section h2 {
  padding-top: 5%;
  color:rgb(242, 241, 238);
  font-size: 3.5rem;
  margin-bottom: 30px;
}

.project-card {
  background: rgba(81, 74, 74, 0.328);
  width:50%;
  margin-left: 25%;
  border: 1px solid #ddd;
  color: white;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 25px;
  transition: transform 0.1s ease;

}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.project-card p {
  margin-bottom: 8px;
}
