
.home-section {
  height: 100vh;
  background-image: linear-gradient(
      rgba(0, 0, 0, 0.5), 
      rgba(0, 0, 0, 0.5)
    ),
    url('E:\REACT\Portfolio\my-app\src\Images\Home.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
}

.home-section h1 {
  font-size: 5rem;
  margin-bottom: 20px;
}

.home-section p {
  font-size: 2rem;
  margin-bottom: 30px;
  max-width: 700px;
}

.home-section .btn {
  padding: 20px 35px;
  background-color: #00bcd4;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 2rem;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.home-section .btn:hover {
  background-color: #008fa1;
}
